$verde: #1c7a1f;
$branco: #ffffff;
$preto: #000000;
$vermelho: #ff1a1a;

.ranking-modal {
	background-color: $branco;
	display: inline-flex;
	width: 80vw;
	height: fit-content;
	border-radius: 9999px;
	margin-bottom: 1.5rem;
	justify-content: space-between;
	box-shadow: 1px 1px 10px $preto;

	.student-ranking {
		display: inline-flex;

		.ranking-modal-image-container {
			background-color: $vermelho;
			width: 8rem;
			height: 8rem;
			border-radius: 50%;
			display: flex;
			margin-right: 2vw;

			.ranking-modal-image {
				margin: auto;
				width: 7rem;
				height: 7rem;
			}
		}

		.student-ranking-info {
			display: flex;
			flex-direction: column;
			width: 40vw;
			height: 100%;
			color: $verde;
			justify-content: space-evenly;

			h1 {
				font-weight: 900;
				font-size: 2rem;
				line-height: 2.5rem;
			}

			h2 {
				font-size: 1.75rem;
				line-height: 1.25rem;
			}
		}
	}

	.student-ranking-pontuacao {
		align-self: center;
		text-align: center;
		margin-right: 2rem;
		height: fit-content;
		color: $verde;
		font-weight: 900;

		h1 {
			font-size: 3rem;
			font-weight: 900;
		}

		h2 {
			font-size: 2rem;
			font-weight: 900;
		}
	}
}

@media screen and (max-width: 768px) {
	.ranking-modal {
		border-radius: 14px;

		.student-ranking {
			.ranking-modal-image-container {
				width: 7rem;
				height: 7rem;
				border-radius: 14px;

				.ranking-modal-image {
					width: 6rem;
					height: 6rem;
				}
			}
		}
	}
}

@media screen and (max-width: 650px) {
	.ranking-modal {
		padding: 20px;
		.student-ranking {
			.ranking-modal-image-container {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 480px) {
	.ranking-modal {
		height: 15vh;
		border-radius: 14px;

		.student-ranking {
			.student-ranking-info {
				h1 {
					font-weight: 800;
				}

				h2 {
					font-size: 1.5rem;
				}
			}

			.ranking-modal-image-container {
				display: none;
			}
		}

		.student-ranking-pontuacao {
			h1 {
				font-weight: 800;
				font-size: 3rem;
			}
		}
	}
}
